.recipe {
  width: 10%;
  border-radius: 10px;
  box-shadow: 0px 5px 20px black;
  margin: 50px 20px 0 20px;
  background: white;
  align-items: center;
  min-width: 20%;
  font-family: "Raleway", sans-serif;
  font-weight: 400;
}

.recipe h1 {
  position: relative;
  padding: 15px;
  font-size: 2vw;
  text-decoration: underline;
  font-family: "Rock Salt", cursive;
  font-weight: 800;
  text-align: center;
}

.recipe ul {
  text-align: center;
  font-size: 1vw;
  padding: 10px;
  list-style-type: none;
}

.recipe p {
  font-weight: bold;
  text-align: center;
}

.recipeImg {
  text-align: center;
}

.foodImage {
  border-radius: 10px 10px 0 0;
  text-align: center;
  position: relative;
  width: 100%;
}

.App {
  min-height: 100vh;
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#f2ff00+4,ff9532+100 */
  background: #f2ff00; /* Old browsers */ /* FF3.6-15 */ /* Chrome10-25,Safari5.1-6 */
  background: radial-gradient(
    ellipse at center,
    #f2ff00 4%,
    #ff9532 100%
  ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f2ff00', endColorstr='#ff9532',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
}

h1 {
  padding-top: 20px;
  font-family: "Rock Salt", cursive;
  font-size: 4vw;
}

h4 {
  padding-top: 10px;
  font-size: 2vw;
  font-family: "Raleway", sans-serif;
  font-weight: bold;
}

h1,
h4 {
  text-align: center;
}

.search-form {
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-bar {
  width: 50%;
  border: none;
  padding: 10px;
  font-family: "Raleway", sans-serif;
}

.search-button {
  background: lightcoral;
  border: none;
  padding: 10px 20px;
  color: white;
}

.recipies {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

